import React, { Component } from 'react';
import SpacetimeWatch from './spacetime-watch';
import './App.css';

class App extends Component {

  render() {
    let canvas={x:500,y:500};

    return (
      <div className="App">
        <SpacetimeWatch id="1" canvas={canvas} view={canvas} bdate="1967-01-09T08:00:00.000Z" />
      </div>
    );
  }
}

export default App;



