import React from 'react';
import CircularBands from './circular-bands.js';
import Space         from './space.js';
import Time          from './time.js';

class SpacetimeWatch extends React.Component {
  constructor(props) {
    super(props);
    this.spec = {
      id: this.props.id,
      ctr: {
        x: this.props.view.x/2,
        y: this.props.view.y/2
      },
      r_max:  0,  // maximum radius
      bw_max: 0,  // maximum band width
      r_b:    0,  // radius of the band - array
      bdate: this.props.bdate
    };
    this.spec.r_max  = Math.min(this.spec.ctr.x, this.spec.ctr.y)*.99; 
    this.spec.bw_max = this.spec.r_max*.34;   // 34% of the outer side radius will be used as the band area
    const bw_ratio = [0,    // radial points of each band, 0 is the outermost band
      1,      // Year-Band (60 years)
      3,      // Month-Band (12 months)
      .6,     // Day-Band (365 or 366 days)
      4,      // Hour-Band (12 hours)
      .5];    // Circular Progressive Bar: innermost band
    // unit distance between circles
    const bwu = this.spec.bw_max/bw_ratio.reduce((a, v) => (a + v));
    let rh    = this.spec.r_max;
    this.spec.r_b = bw_ratio.map(r => (rh -= r*bwu));   // converts width to the radial points
  }

  render() {
    // console.log(`SpacetimeWatch:render(${this.spec.id})`);
    return (
      <div className="dev-iPad">
        <svg width={this.props.canvas.x} height={this.props.canvas.y}
          viewBox={`0 0 ${this.props.view.x} ${this.props.view.y}`}
          style={{backgroundColor: "white"}}>
          <defs>
            <filter id="blursun">
              <feGaussianBlur in="SourceGraphic" stdDeviation="2" result="blur5" />
              <feComposite in="SourceGraphic" in2="blur5" operator="arithmetic" k1="0" k2="3" k3="3" k4="0" />
            </filter>
          </defs>
          <CircularBands spec={this.spec} />
          <Space         spec={this.spec} />
          <Time          spec={this.spec} />
        </svg>
      </div>
    );
  }
}

export default SpacetimeWatch;




